* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

.slide {
  opacity: 0;
  transition-duration: 0.6s ease;
  max-width: 100vh;
}

.slide.active {
  opacity: 1;
  transition-duration: 0.6s;
  transform: scale(1.08);
  max-width: 100vh;
}

.slider-button{
  box-shadow: rgba(61,165,120, .1) 0 0 0 1px inset, rgba(61,165,120, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(61,165,120, .3) 0 0 0 4px;
}

.logo2{
  margin-top: 50px;
}